.location
    width: 100%
    height: 230vh
    background: url('../assets/Map.png')
    background-position: center
    background-size: cover
    display: flex
    flex-direction: column
    align-items: center
    justify-content: space-between

    .location__up

        .location__title
            text-transform: uppercase
            font-size: 1.1rem
            text-align: center
            margin-top: 70px
            margin-bottom: 0
            position: relative
            color: $color-primary
            &::after
                content: ''
                position: absolute
                left: 40%
                bottom: -20px
                width: 20%
                background: $color-orange
                height: 7px

        .location__subtitle
            text-transform: capitalize
            margin-top: 80px
            font-size: 3rem
            font-weight: 400
            color: $color-primary

    .location__down

        .location__text
            text-align: center
            font-size: 2rem
            font-weight: 300
            color: $color-primary
            position: relative
            bottom: 80px

        .location__button
            text-transform: uppercase
            display: flex
            justify-content: center
            align-items: center
            line-height: .7rem
            position: relative
            bottom:70px
            a
                color: white
                font-size: .6rem
                background: $color-orange
                padding: 10px 30px
                transition: 0.3s
                text-decoration: none
                &:hover
                    font-size: .7rem
                    font-weight: 700
                    text-decoration: underline

