.values
    width: 100%
    height: 120vh
    background: url('../assets/PanelSolar.png')
    background-size: cover
    background-position: top
    margin: 40px 0
    display: grid
    grid-template-columns: 1fr
    grid-template-rows: auto
    grid-template-areas: ".values__title" ".values__wrapp" ".copyright"

    .values__title
        grid-area: values__title
        text-transform: uppercase
        font-size: 1.1rem
        text-align: center
        margin-bottom: 0
        position: relative
        top: 25%
        color: white
        &::after
            content: ''
            position: absolute
            left: 47.8%
            top: 30px
            width: 5%
            background: $color-orange
            height: 7px

    .values__wrapp
        grid-area: values__wrapp
        width: 60%
        height: 90%
        background: rgba(white, .8 )
        margin: auto
        padding: 20px 60px
        text-align: center
        color: $color-primary
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center

        .values__wrapp-title
            font-size: 3rem
            margin: 0
            padding-bottom: 20px
            font-weight: 400
        .values__wrapp-p
            font-size: 1.2rem

    .copyright
        grid-area: copyright
        text-align: center
        color: white
        font-size: .8rem
        p
            background: rgba(black, .5 )