.footer
    background: $color-footer
    height: 65vh

    .footer__wrapper
        display: grid
        grid-template-columns: 1fr
        grid-template-rows: 120px auto
        color: white

        .footer__logo
            width: 30px
            height: auto
            display: flex
            align-items: flex-end
            img
                height: auto
                width: 100%

        .footer__row1
            display: flex
            justify-content: space-between
            border-bottom: 1px solid #009872

            .footer__row1-contact
                text-transform: uppercase
                font-weight: 400

            .footer__row1-redes
                display: flex
                align-items: center

                .footer__row1-redes-icons
                    font-size: 1.3rem
                    margin-right: 10px
                    a
                        color: $color-orange
                        cursor: pointer
                    i
                        margin-left: 10px

                .footer__row1-redes-text
                    text-transform: uppercase

        .footer__row2
            display: flex
            justify-content: space-between

            .footer__row2-text
                text-align: left
                p
                    &:nth-child(1)
                        font-weight: bold

            .footer__row2-info
                text-align: right
