.history
    width: 100%
    height: 120vh
    background: url('../assets/Oranges.jpg')
    background-position: center
    background-size: cover
    background-repeat: no-repeat
    display: flex
    align-items: center
    justify-content: center

    .history-capa
        height: 90%
        width: 100%
        background: rgba($color-orange, .8)
        margin: auto auto
        display: flex
        align-items: center
        justify-content: center

        .history__text
            display: flex
            flex-direction: column
            justify-content: center
            align-items: center
            color: white

            .history__text-title
                text-transform: uppercase
                font-size: 1rem
                margin-top: 70px
                position: relative
                &::after
                    content: ''
                    position: absolute
                    left: 5%
                    bottom: -20px
                    width: 90%
                    background: $color-primary
                    height: 7px

            .history__text-subtitle
                text-transform: capitalize
                font-size: 2.5rem
                font-weight: 400
                padding-top: 10px

            .history__text-p
                text-align: center
                font-size: 1.2rem